body{
	height:100vh;
	width:100vw;
	margin:0;
	overflow:hidden;
}

#root {
	height:100%;
	width:100%;
}

#main-canvas {
	height:100%;
	width:100%;
	background-color:black;
	z-index:0;
	
}

#jet-plane {
	height:10vh;
	width:10vh;
	position:absolute;
	z-index:104;
	user-select:none;
}

.arrow {
	z-index:102;
	height:10vh;
	width:10vh;
	position:absolute;
	background:none;
	user-select:none;
}


.bug {
	height:10vh;
	width:10vh;
	z-index:101;
	background:none;
	position:absolute;
	user-select:none;
}



